#contacts {
    padding: 50px 0 20px;
    background-color: #565656;
    position: relative;

    .up-button {
        position: absolute;
        bottom: 40px;
        width: 75px;
        height: 75px;
        display: flex;
        justify-content: center;
        align-items: center;
        right: 10%;
        background-color: transparent;
        border: none;
        cursor: pointer;

        &:hover{
            svg {      
                g {
                    fill: #fe8708;
                    transition: .3s;
                    opacity: .8;
                }
            }
        }

    }

    .footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .footer-name {
        font-size: 32px;
        font-style: italic;
        font-weight: bold;
    }

    .footer-info {
        h2 {
            margin-bottom: 20px;
        }
    }

    .info-item {
        display: flex;
        align-items: center;
        margin-bottom: 15px;

        img {
            width: 30px;
        }
    }

    .info-text {
        text-align: center;
        margin-left: 15px;

        a {
            color: #000;

            &:hover {
                color: #fe8708;
                transition: .3s;
            }
        }
    }
}