#our-works {
    padding: 50px 0 100px;
    background-color: #565656;

    .works-card {
        height: 500px;
        border-radius: 25px;
        overflow: hidden;
        margin: 0 10px;
    }

    .works-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .works-arrows {
        padding-top: 30px;
        text-align: center;

        .slick-arrow {
            font-size: 0;
            width: 92px;
            height: 92px;
            cursor: pointer;
            border: none;
            background: center no-repeat transparent;
            background-image: url(assets/img/arrow.svg);

            &:hover {
                transition: .3s;
                background-image: url(assets/img/arrow-hover.svg);
            }
        }

        .slick-next {
            transform: rotate(180deg);
            margin-left: 50px;
        }
    }
}