@media screen and (max-width: 1199px) {
    #first-screen{

        .slide-text {
            font-size: 32px;
            max-width: 700px;
        }

        .scroll-down {
           width: 30px;
           height: 30px;

            img {
            width: 20px;
            left: 25%;
            }
        }

       .slick-dots {
        
            li {
                margin: 0 6px;

                button {
                    padding: 6px;
                }
            } 

            .slick-active {
                button {
                   padding: 6px 10px;
               }
            }
        }
    }

    .title-template {
        font-size: 34px;
    }

    .call-fixed {
        display: none;
    }
}
