@media screen and (min-width: 768px) {
    .header-nav {
        .hover-case {
            font-size: 16px;
            margin: 0 20px;
        }
    }

    .hamburger,
    .btn_call {
        display: none;
    }

    .slick-slide { 
        img {
            width: 100vw;
            height: 100vh;
            object-fit: cover;
        }
    }
}
