#first-screen{

  .slick-slide {
    height: 100vh;
    position: relative;
  
    img{
      filter: brightness(0.5);
    }
  }
  
  .slide-text {
    position: absolute;
    backdrop-filter: blur(15px);
    border-radius: 25px;
    color: #ffffff;
    padding: 15px;
    font-size: 44px;
    max-width: 1200px;
    text-align: right;
    right: 5%;
    top: 35%;
  }
  
  .scroll-down {
    position: absolute;
    top: 83%;
    left: 50%;
    width: 50px;
    height: 50px;
    backdrop-filter: blur(15px);
    transform: rotate(45deg) translate(-50%, 0);
    cursor: pointer;
  
    img {
      width: 30px;
      position: absolute;
      left: 30%;
      top: 38%;
      transform: rotate(-45deg);
    }
  
    &:hover {
      opacity: .7;
    }
  }
  
  .slick-dots {
    display: flex;
    position: absolute;
    list-style: none;
    bottom: 3%;
    left: 50%;
    transform: translate(-50%, 0);

    .slick-active {
      button {
        padding: 8px 12px;
        background-color: #ffbe5390;
      }
    }

    li {
      font-size: 0;
      padding: 0;
      margin: 0 8px;

      button {
        font-size: 0;
        padding: 8px;
        border-radius: 4px;
        border: 1px solid #fff;
        cursor: pointer;
        background-color: transparent;
        transition: 0.3s;

        &:hover {
          background-color: #ffbe5390;
        }
      }
    }
  }
}