@media screen and (max-width: 767px) {
    #first-screen {
        .slide-text {
            font-size: 20px;
            max-width: 90%;
        }
    }

    .header-logo {
        margin-right: auto;
        width: 120px;
    }

    .header-nav,
    .btn_call {
        display: none;
    }

    header.show {
        .header-nav {
            display: inline-block;
            position: absolute;
        }

        .btn_call {
            display: flex;
            position: absolute;
            top: 50%;
        }

        .nav-1 {
            top: 20%;
        }

        .nav-2 {
            top: 27%;
        }

        .nav-3 {
            top: 34%;
        }

        .nav-4 {
            top: 41%;
        }
    }

    .slick-slide {
        .img-fluid {
            width: 100vw;
            height: 100vh;
            object-fit: cover;
        }
    }

    .label-section {
        width: 130px;
        left: -38px;
        top: 36.5px;
        font-size: 14px;
    }

    #first-screen {
        .slick-dots {
            li {
                button {
                    outline: none;
                }
            }
        }
    }

    .title-template {
        font-size: 24px;
        width: 300px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 30px;
    }

    .subtitle-template {
        font-size: 22px;
        margin-bottom: 30px;
    }

    #about-company {
        padding: 50px 0;

        .label-section {
            top: 36.5px;
        }

       .grade-box {
            margin-bottom: 30px;

            .grade-img {
                width: 50px;
                height: 50px;
            }

            .grade-text {
                font-size: 12px;
            }
        }

        .about-text-box {
            flex-direction: column;
            margin-bottom: 20px;
        }

        .first-box,
        .second-box {
            width: auto;

            p {
                margin-bottom: 10px;
            }
        }

        .first-box {
            font-size: 24px;
            margin-bottom: 20px;
        }

        .second-box {
            font-size: 14px;
        }

        .label-title {
            font-size: 26px;
        }

        #about-company .label-subtitle {
            font-size: 12px;
        }
    }

    #partner {
        padding: 50px 0;

        .partner {
            flex: 0 1 130px;
            img {
                max-width: 100%;
            }
        }              
    }

    .footer-logo {
        width: 150px;

        img {
            max-width: 100%;
        }
    }

    #contacts {

        .footer {
            align-items: flex-start;
        }

        .footer-name {
            font-size: 16px;
        }

        .footer-info {
            h2 {
                font-size: 20px;
                margin-bottom: 10px;
            }
        }

        .info-text {
            font-size: 12px;
            margin-left: 10px;
        }

        .info-item {
            img {
                width: 20px;
            }
        }

        .up-button {
            width: 50px;
            height: 50px;
            bottom: 25px;
            left: 17%;
            right: 0;
        }
    }
}
