#about-company {
    padding: 40px 0;

    .label-section {
        top: 10px;
    }

    .grade-box {
        display: flex;
        margin-bottom: 40px;

        .grade-card {
            width: 20%;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            padding: 15px;
        }

        .grade-img {
            width: 100px;
            height: 100px;
            margin-bottom: 25px;
        }

        .grade-text {
            font-size: 20px;
        }
    }

    .about-text-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 70px;
    }

    .first-box {
        font-size: 42px;
        width: 450px;
        font-weight: bold;

        p {
            margin-bottom: 20px;
        }
    }
    
    .second-box {
        width: 650px;
        font-weight: bold;
        font-size: 20px;
        line-height: 22px;

        p {
            margin-bottom: 20px;
        }
    }

    .label-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .about-label {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: -12px;
            left: -15px;
            width: calc(100% + 30px);
            height: calc(100% + 30px);
            border-radius: 50%;
            background-color: rgba(255, 255, 255, 0.3);
            filter: blur(10px);
            z-index: 0;
        }
    }

    .label-title {
        font-size: 60px;
        color: #fe8708;
        z-index: 1;
    }

    .label-subtitle {
        font-size: 20px;
        font-weight: bold;
        z-index: 1;
    }
}