@media screen and (max-width: 1399px) {
    #first-screen {

        .slide-text{
           font-size: 38px;
           max-width: 900px;
       }
    
       .scroll-down {
           top: 82%;
       }
    }

    header {
        height: 110px;
    }

    .header-nav {
        .hover-case {
            font-size: 20px;
        }
    }

    .header-logo {
        width: 160px;
    }
    
    .label-section {
        width: 210px;
        left: -71px;
        top: 71px;
        font-size: 26px;
    }

    .title-template {
        font-size: 38px;
    }

    #about-company {

        .first-box {
            font-size: 40px;
        }
    }

    .footer-info {
        h2 {
            font-size: 30px;
        }
    }

    #contacts {
        .info-text {
            font-size: 18px;
        }

        .info-item {
            img {
                width: 25px;
            }
        }

        .footer-name {
            font-size: 30px;
        }

        #contacts .up-button {
            width: 60px;
            height: 60px;
        }
    }  
}