@media screen and (max-width: 1024px) {
    header {
        height: 90px;
    }

    .header-nav {
        .hover-case {
            font-size: 18px;
        }
    }

    .header-logo {
        width: 140px;
    }

    #first-screen {
        .slide-text {
            font-size: 26px;
            max-width: 650px;
        }

        .slick-dots {
            li {
                button {
                    outline: none;
                }
            }
        }
    }

    .title-template {
        font-size: 30px;
    }

    .label-section {
        width: 170px;
        left: -54px;
        top: 54px;
        font-size: 20px;
    }

    #about-company {
        .grade-box {

            .grade-img {
                width: 60px;
                height: 60px;
            }

            .grade-text {
                font-size: 16px;
            }
        }

        .first-box {
            font-size: 30px;
            width: 440px;
        }

        .second-box {
            width: 500px;
            font-size: 16px;
        }
        
        .label-title {
            font-size: 40px;
        }

        .label-subtitle {
            font-size: 16px;
        }
    }

    .subtitle-template {
        font-size: 28px;
    }

    #our-works {
        padding: 50px 0;

        .works-card {
            height: 400px;
            margin: 0 6px;
        }

        .works-slider {
            padding-top: 25px;
        }
    }

    #partner {
        .partner {
            img {
                max-width: 70%;
            }
        }
    }

    #contacts .up-button {
        bottom: 20px;
        width: 50px;
        height: 50px;
        right: 5%;
    }
}