#partner {
    padding: 100px 0;

    .partners {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 20px;
        max-width: 80%;
        margin-left: auto;
        margin-right: auto;
      }
      
      .partner {
        flex: 1 1 200px;
        text-align: center;
      }
      
      .partner img {
        max-width: 100%;
        height: auto;
      }
}