header {
    height: 130px;
    width: 100%;
    position: absolute;
    z-index: 2;
    backdrop-filter: blur(15px);
}

.header {
    padding: 6px 0;
    display: flex;
    align-items: center;
    justify-content: center;

    &-logo {
        width: 200px;
    }

    &-nav {

        .hover-case {
            font-size: 22px;
            text-transform: uppercase;
            position: relative;
            color: #fff;
            margin: 0 34px;
            text-decoration: none;

            &::before {
                content: "";
                display: inline-block;
                background: #ffbd51;
                width: 0px;
                height: 4px;
                position: absolute;
                transition: .3s;
                bottom: -10px;
                left: 50%;
                transform: translateX(-50%);
            }

            &:hover {
                color: #ffbd51;
                transition: .3s;

                &:before {
                    width: 100%;
                }
            }
        }
    }
}

.btn_call {
    padding: 8px 16px;
    display: flex;
    align-items: center;
    background-color: transparent;
    border: 1px solid #fff;
    border-radius: 25px;
    text-decoration: none;

    img {
        max-width: 35px;
        margin-right: 10px;
    }

    h6 {
        color: #fff;
        font-size: 18px;
        font-weight: 500;
    }

    &:hover,
    &:active, 
    &:focus,
    &:focus-visible {
        background-color: #ffbd51;
    }
}