::selection {
    color: #000;
    background-color: #ffbe53;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #c0c0c0;
    font-family: $font-primary;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-weight: 700;
    line-height: 126%;
}

h1,
.h1 {
    font-size: 56px;
}

h2,
.h2 {
    font-size: 36px;
}

h3,
.h3 {
    font-size: 24px;
}

h4,
.h4 {
    font-size: 22px;
}

h5,
.h5 {
    font-size: 20px;
}

h6,
.h6 {
    font-size: 18px;
}

.fw-300 {
    font-weight: 300;
}

.fw-400 {
    font-weight: 400;
}

.fw-600 {
    font-weight: 600;
}

.fw-700 {
    font-weight: 700;
}

.img-fluid {
    width: 100%;
    height: auto;
}

section {
    position: relative;
}

.label-section {
    background-color: #fe8708;
    transform: rotate(270deg);
    width: 250px;
    padding: 20px;
    position: absolute;
    left: -88px;
    top: 88px;
    text-align: center;
    color: #c0c0c0;
    font-size: 32px;
}

.container {
    max-width: 1170px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.title-template {
    text-align: center;
    position: relative;
    text-transform: uppercase;
    padding-bottom: 19px;
    font-size: 42px;
    font-weight: bold;
    margin-bottom: 80px;

    &::after,
    &::before {
        content: "";
        position: absolute;
        height: 2px;
        background-color: #fe8708;
        box-shadow: 0px 3px 3px 0px rgba(254,135,8,0.75);
    }

    &::after {
        bottom: 15px;
        left: calc(50% - 25%);
        width: 50%;
    }

    &::before {
        bottom: 0;
        left: 35%;
        width: 30%;
    }
}

.subtitle-template {
    text-transform: uppercase;
    color: #fe8708;
    text-shadow: 2px 2px 2px rgba(254, 135, 8, 0.6);
    padding-top: 10px;
    font-size: 34px;
    margin-bottom: 50px;  
}


.show {
    height: 100vh;
    width: 100vw;
    transition: 1.2s;
    background-color: #565656;
}

.call-fixed {
    background-color: #ffbe53;
    padding: 15px;
    margin-left: 92%;
    top: 85%;
    border-radius: 50%;
    z-index: 50;
    width: 75px;
    position: sticky;
    opacity: .8;

    img {
        max-width: 100%;
    }
}

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
    background-color: #ffbe53;
}

.hamburger-inner::before {
    width: 30px;
}

.hamburger.is-active .hamburger-inner, .hamburger.is-active .hamburger-inner::before, .hamburger.is-active .hamburger-inner::after {
    background-color: #ffbe53;
}